import { BiPlugin, BiPluginConfig } from '../../types';
import onCangjieFocus from './hooks/onCangjieFocus';
import createRenderContent from './hooks/createRenderContent';
import createOnAction from './hooks/onAction';
import Actions, { enterPreview, exitPreview } from './actions';
import isQuickPreview from './queries/isQuickPreview';
import shouldRenderH5Controls from './queries/shouldRenderH5Controls';
import selectionColor from './queries/selectionColor';

function QuickPreviewPlugin(configs?: BiPluginConfig): BiPlugin {
  return {
    queries: { isQuickPreview, selectionColor, shouldRenderH5Controls },
    onAction: createOnAction(configs?.quickPreview),
    onCangjieFocus,
    renderContent: createRenderContent(configs?.quickPreview),
    actions: {
      enterPreview,
      exitPreview,
    },
  };
}

QuickPreviewPlugin.Actions = Actions;

export default QuickPreviewPlugin;
